import * as _ from 'lodash';
import { module } from './module';

export class Controller {

    selected:Array<any>;
    key:string;

    constructor() {
        this.selected =  this.selected || [];
        this.key = this.key || "key";
    }

    isChecked(item){
        return _.some(this.selected,(selectItem)=>{
            return item[this.key] == selectItem[key];
        });
    }

    toggle(item){
        let remove = _.remove(this.selected,(selectItem)=>{
            return item[this.key] == selectItem[key];
        });

        if(!remove.length){
            this.selected.push(item);
        }
    }

}

function Directive(): ng.IDirective {
    return {
        restrict: 'EA',
        scope: false,
        bindToController: {
            "key":"@?",
            "selected":"="
        },
        controller: Controller
    }
}

module.directive("modandact", [Directive]);


